import gql from "graphql-tag";
import qraphql from "@/apollo-client";

function toWithId(params: string[]) {
  return params.map((el, index) => ({
    value: index + 1,
    text: el,
  }));
}

class AccidentModel {
  id = null;
  static defaultData = {
    generalInfo: {
      num: "",
      note: "",
      date: "",
      time: "",
      category: "",
      view: "",
    },
    roadCondition: {
      view: "",
      condition: "",
      illumination: "",
    },

    vehicle: {
      model: "",
      technicalPassport: "",
      plateNumber: "",
      engineNumber: "",
      bodyNumber: "",
      yearManufacture: "",
      location: "",
      typeCar: 0,
      typeDrive: 0, //  
      typeCargo: 0,
      steeringArrangement: "",
      color: "",
      trailerPresence: false, //  
      technicalFault: 0,
    },

    people: {
      gender: "",
      birthday: "",
      category: "",
      location: "",
      losses: "",
      violation: "",
      belt: "",
      experience: "",
    },
  };

  options = {
    generalInfo: {
      category: toWithId(["    ", "   ", "  ", "      -", ""]),
    },
    vehicle: {
      typeCar: [
        { value: 10, text: "" },
        { value: 20, text: " " },
        { value: 30, text: " " },
        { value: 40, text: "" },
        { value: 50, text: "" },
        { value: 60, text: "" },
        { value: 61, text: "  " },
        { value: 62, text: "  " },
        { value: 70, text: "" },
        { value: 80, text: "" },
      ],
      typeDrive: toWithId(["", "", ""]),
      typeCargo: toWithId(["", " / ", ""]),
      color: [
        { value: 1, text: "" },
        { value: 5, text: " " },
        { value: 10, text: "" },
      ],
      technicalFault: toWithId([" ", " ", " "]),
      trailerPresence: ["", ""],
    },
    people: {
      gender: toWithId(["", ""]),
      category: toWithId(["", "", "", ""]),
      losses: toWithId(["", "", ". "]),
    },
  };

  getValueByOptions(value: any, options: { value: number; text: string }[]) {
    return options.find(el => el.value === value)?.text;
  }
  getValueByOptionsBool(value: any, options: { value: number; text: string }[]) {
    return value ? options[0] : options[1];
  }

  async load(paramId: any) {
    const extractId = paramId.id;

    if (this.id !== null || extractId !== undefined) {
      this.id = this.id === null ? Number(extractId) : this.id;
      const response = await qraphql.query({
        query: gql`
          query($id: Int!) {
            accident_crash(where: { id: { _eq: $id } }) {
              vehicle
              people
              generalInfo
              geom
            }
          }
        `,
        variables: {
          id: this.id,
        },
      });

      const {
        data: { accident_crash },
      } = response;
      if (accident_crash.length <= 0) {
        this.id = null;
      }
      return accident_crash.length > 0 ? accident_crash[0] : [];
    } else {
      return [];
    }
  }

  async save(vehicle: any[], people: any[], generalInfo: object, geom: object) {
    const id = this.id;

    const objects = {
      id,
      vehicle,
      people,
      generalInfo,
      geom,
    };

    if (id === null) {
      delete objects.id;
    }

    const response = await qraphql.mutate({
      mutation: gql`
        mutation($objects: [accident_crash_insert_input!]!) {
          insert_accident_crash(on_conflict: { constraint: crash_pkey, update_columns: [vehicle, people, generalInfo, geom] }, objects: $objects) {
            affected_rows
            returning {
              id
            }
          }
        }
      `,
      variables: { objects },
    });

    const {
      data: {
        insert_accident_crash: { affected_rows, returning },
      },
    } = response;

    this.id = returning.id;

    return affected_rows;
  }
}

export { AccidentModel };
