




import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import { Map, View, Collection, Feature } from "ol";
import { Tile } from "ol/layer";
import { OSM } from "ol/source";
import { Select, Draw } from "ol/interaction";
import GeometryType from "ol/geom/GeometryType";
import { never } from "ol/events/condition";
import { Point } from "ol/geom";
import { GeoJSON } from "ol/format";
import VectorSource from "ol/source/Vector";
import VectorLayer from "ol/layer/Vector";
import { Style } from "ol/style";
@Component
export default class extends Vue {
  @Prop() value;
  map: Map;
  draw: Draw;
  geoJson = new GeoJSON({
    dataProjection: "EPSG:4326",
    featureProjection: "EPSG:3857"
  });
  layer: VectorLayer;
  mounted() {
    this.map = new Map({
      target: "mapid",
      layers: [
        new Tile({
          source: new OSM()
        })
      ],
      view: new View({
        center: [3066227.1995, 7151697.981],
        zoom: 19
      })
    });

    this.createEvent();
  }

  createEvent() {
    const draw = new Draw({
      type: GeometryType.POINT,
      style: new Style()
    });
    const features = new Collection<Feature<Point>>();
    const source = new VectorSource({
      features
    });
    const layer = new VectorLayer({
      source
    });

    draw.on("drawend", ({ feature }) => {
      const geom = JSON.parse(
        this.geoJson.writeGeometry(feature.getGeometry())
      );
      geom.crs = {
        type: "name",
        properties: {
          name: "EPSG:4326"
        }
      };
      this.$emit("input", geom);
    });

    this.map.addInteraction(draw);
    this.map.addLayer(layer);

    this.draw = draw;
    this.layer = layer;
  }

  @Watch("value")
  onValue(n, o) {
    if (n !== null) {
      const features = this.layer.getSource().getFeaturesCollection();
      features.clear();
      const feature = this.geoJson.readFeature(n);
      features.push(feature as Feature<Point>);
      if (o === null) {
        this.map
          .getView()
          .setCenter((feature.getGeometry() as Point).getCoordinates());
      }

      this.map.updateSize();
    }
  }
}
